<te<template>
  <div>
    <router-link :to="{ name: 'Create' }" class="button is-success mt-5"
      >Incluir</router-link
    >
    <table class="table is-striped is-bordered mt-2 is-fullwidth">
      <thead>
        <tr>
          <th>Nombre</th>
          <th>D.I</th>
          <th>Ficha</th>
          <th>Resultado</th>
          <th>Fec.Res.</th>
          <th>Tipo</th>
          <th class="has-text-centered">Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in items" :key="item.examen_id">
          <td>{{ item.nombre }}</td>
          <td>{{ item.di }}</td>
          <td>{{ item.nficha }}</td>
          <td>{{ item.resultado }}</td>
          <td>{{ item.fecres }}</td>
          <td>{{ item.tipexa }}</td>
          <td class="has-text-centered">
            <router-link
              :to="{ name: 'Edit', params: { id: item.examen_id } }"
              class="button is-info is-small"
              >Edit</router-link>    
            <router-link
              :to="{ name: 'Qr', params: { id: item.examen_id } }"
              class="button is-info is-small"
              >Qr</router-link>  
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
 
<script>
// import axios
import axios from "axios";
 
export default {
  name: "ExamenList",
  data() {
    return {
      items: [],
    };
  },
 
  created() {
    this.getExamenes();
  },
 
  methods: {
    // Get All Examenes
    async getExamenes() {
      try {
        const response = await axios.get("http://localhost:5000/examenes");
        this.items = response.data;
      } catch (err) {
        console.log(err);
      }
    },
 
    // Genera codigo QR
    async GeneraQR(id) {
      try {
        await axios.get(`http://localhost:5000/examenes/${id}`);
        this.getExamenes();
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>
 
