<template>
  <div>
    <div class="field">
      <label class="label">Examen</label>
      <div class="control">
        <input
          class="input"
          type="text"
          placeholder="Examen Nombre"
          v-model="examenNombre"
        />
      </div>
    </div>
 
    <div class="field">
      <label class="label">D.I</label>
      <div class="control">
        <input
          class="input"
          type="text"
          placeholder="D.I"
          v-model="examenDi"
        />
      </div>
    </div>

    <div class="field">
      <label class="label">Genero</label>
      <div class="control">
        <input
          class="input"
          type="text"
          placeholder="Genero"
          v-model="examenGenero"
        />
      </div>
    </div>

    <div class="field">
      <label class="label">Fec.Nac.</label>
      <div class="control">
        <input
          class="input"
          type="text"
          placeholder="Fec.Nac."
          v-model="examenFecnac"
        />
      </div>
    </div>

    <div class="field">
      <label class="label">Ficha</label>
      <div class="control">
        <input
          class="input"
          type="text"
          placeholder="Ficha"
          v-model="examenNficha"
        />
      </div>
    </div>

    <div class="field">
      <label class="label">Resultado</label>
      <div class="control">
        <input
          class="input"
          type="text"
          placeholder="Resultado"
          v-model="examenResultado"
        />
      </div>
    </div>

    <div class="field">
      <label class="label">Fec.Res.</label>
      <div class="control">
        <input
          class="input"
          type="text"
          placeholder="Fec.Res."
          v-model="examenFecres"
        />
      </div>
    </div>

    <div class="field">
      <label class="label">Tipo Examen</label>
      <div class="control">
        <input
          class="input"
          type="text"
          placeholder="Tipo Examen"
          v-model="examenTipexa "
        />
      </div>
    </div>

    <div class="field">
      <label class="label">Fec.T.M.</label>
      <div class="control">
        <input
          class="input"
          type="text"
          placeholder="Fec.T.M."
          v-model="examenFectm "
        />
      </div>
    </div>  

    <div class="field">
      <label class="label">Fec.Rec.</label>
      <div class="control">
        <input
          class="input"
          type="text"
          placeholder="Fec.Rec."
          v-model="examenFecrec"
        />
      </div>
    </div>       

    <div class="field">
      <label class="label">Conclusión</label>
      <div class="control">
        <input
          class="input"
          type="text"
          placeholder="Conclusión"
          v-model="examenConclu"
        />
      </div>
    </div>  

    <div class="control">
      <button class="button is-success" @click="updateExamen">Grabar</button>
    </div>
  </div>
</template>
 
<script>
// import axios
import axios from "axios";
 
export default {
  name: "EditExamen",
  data() {
    return {
      examenNombre: "",
      examenDi: "",
      examenGenero: "",
      examenFecnac: "",
      examenNficha: "",
      examenResultado: "",
      examenFecres: "",
      examenTipexa: "",
      examenFectm: "",
      examenFecrec: "",
      examenConclu: "",
    };
  },
  created: function () {
    this.getExamenById();
  },
  methods: {
    // Get Examen By Id
    async getExamenById() {
      try {
        const response = await axios.get(
          `http://localhost:5000/examenes/${this.$route.params.id}`
        );
        this.examenNombre = response.data.nombre;
        this.examenDi = response.data.di;
        this.examenGenero = response.data.genero;
        this.examenFecnac = response.data.fecnac;
        this.examenNficha = response.data.nficha;
        this.examenResultado = response.data.resultado;
        this.examenFecres = response.data.fecres;
        this.examenTipexa = response.data.tipexa;
        this.examenFectm = response.data.fectm;
        this.examenFecrec = response.data.fecrec;
        this.examenConclu = response.data.conclu;
      } catch (err) {
        console.log(err);
      }
    },
 
    // Update examen
    async updateExamen() {
      try {
        await axios.put(
          `http://localhost:5000/examenes/${this.$route.params.id}`,
          {
            nombre: this.examenNombre,
            di: this.examenDi,
            genero: this.examenGenero,
            fecnac: this.examenFecnac,
            nficha: this.examenNficha,
            resultado: this.examenResultado,
            fecres: this.examenFecres,
            tipexa: this.examenTipexa,
            fectm: this.examenFectm,
            fecrec: this.examenFecrec,
            conclu: this.examenConclu,
          }
        );
        this.examenNombre = "";
        this.examenDi = "";
        this.examenGenero = "";
        this.examenFecnac = "";
        this.examenNficha = "";
        this.examenResultado = "";
        this.examenFecres= "";
        this.examenTipexa = "";
        this.examenFectm = "";
        this.examenFecrec = "";
        this.examenConclu = "";
        this.$router.push("/");
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>
 
