<script>
// import axios
import axios from "axios";

export default {
  name: "ExamenQr",
  data() {
    return {
      examenNombre: "",
      examenDi: "",
    };
  },
  created: function () {
    this.getExamenById();
  },
  methods: {
    // Get Examen By Id
    async getExamenById() {
      try {
        const response = await axios.get(
          `http://localhost:5000/examenes/${this.$route.params.id}`
        );
        const moment = require('moment')
        this.examenNombre = response.data.nombre;
        this.examenDi = response.data.di;
        this.examenGenero = response.data.genero;    
        this.examenFecnac = moment(response.data.fecnac).format("DD/MM/YYYY");
        this.examenNficha = response.data.nficha;
        this.examenResultado = response.data.resultado;
        this.examenFecres = moment(response.data.fecres).format("DD/MM/YYYY");
        this.examenTipexa = response.data.tipexa;
        this.examenFectm = moment(response.data.fectm).format("DD/MM/YYYY");
        this.examenFecrec = moment(response.data.fecrec).format("DD/MM/YYYY");
        this.examenConclu = response.data.conclu;

      } catch (err) {
        console.log(err);
      }
    },

    // Update examen
    async updateExamen() {
      try {
        await axios.put(
          `http://localhost:5000/examenes/${this.$route.params.id}`,
          {
            nombre: this.examenNombre,
            di: this.examenDi,
            genero: this.examenGenero,
            fecnac: this.examenFecnac,
            nficha: this.examenNficha,
            resultado: this.examenResultado,
            fecres: this.examenFecres,
            tipexa: this.examenTipexa,
            fectm: this.examenFectm,
            fecrec: this.examenFecrec,
            conclu: this.examenConclu,
          }
        );
        this.examenNombre = "";
        this.examenDi = "";
        this.examenGenero = "";
        this.examenFecnac = "";
        this.examenNficha = "";
        this.examenResultado = "";
        this.examenFecres= "";
        this.examenTipexa = "";
        this.examenFectm = "";
        this.examenFecrec = "";
        this.examenConclu = "";

        this.$router.push("/");
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

